@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Poppins', sans-serif;
  background: #f1f5f8;
  color: #102a42;
  line-height: 1.5;
  font-size: .875rem;
}

section {
  width: 90vw;
  margin: 0 auto;
  max-width: 1170px;
}

h1, h2, h3, h4 {
  letter-spacing: .1rem;
  text-transform: capitalize;
  line-height: 1.25;
  margin-bottom: 0.75rem;
}

p {
  margin-bottom: 1.25rem;
  color: #617d98;
}

.menu {
  padding: 5rem 0;
}

.title {
  text-align: center;
  margin-bottom: 2rem;
}

.underline {
  width: 5rem;
  height: 0.25rem;
  background: #c59d5f;
  margin-left: auto;
  margin-right: auto;
}

.btn-container {
  margin-bottom: 4rem;
  display: flex;
  justify-content: center;
}

.filter-btn {
  background: transparent;
  border-color: transparent;
  font-size: 1rem;
  text-transform: capitalize;
  margin: 0 0.5rem;
  letter-spacing: 1px;
  padding: 0.375rem 0.75rem;
  color: #c59d5f;
  cursor: pointer;
  transition: all .3s linear;
  border-radius: 0.25rem;
}

.filter-btn:hover {
  background: #c59d5f;
  color: #fff;
}

.section-center {
  width: 90vw;
  margin: 0 auto;
  max-width: 1170px;
  display: grid;
  grid-gap: 3rem 2rem;
  gap: 3rem 2rem;
  justify-items: center;
}

.menu-item {
  display: grid;
  grid-gap: 1rem 2rem;
  gap: 1rem 2rem;
  max-width: 25rem;
}

.photo {
  object-fit: cover;
  height: 200px;
  width: 100%;
  border: 0.25rem solid #c59d5f;
  border-radius: 0.25rem;
  display: block;
}

.item-info header {
  display: flex;
  justify-content: space-between;
  border-bottom: 0.5px dotted #617d98;
}

.item-info h4 {
  margin-bottom: 0.5rem;
}

.price {
  color: #c59d5f;
}

.item-text {
  margin-bottom: 0;
  padding-top: 1rem;
}

@media screen and (min-width: 800px){
  body, h4 {
      font-size: 1rem;
  }
}

@media screen and (min-width: 992px){
  section {
      width: 95vw;
  }
}

@media screen and (min-width: 1200px){
  .section-center {
      width: 95vw;
      grid-template-columns: 1fr 1fr;
  }
}

@media screen and (min-width: 800px){
  h1, h2, h3, h4 {
      line-height: 1;
  }
}

@media screen and (min-width: 800px){
  h2 {
      font-size: 2.5rem;
  }
}

@media screen and (min-width: 768px){
  .menu-item {
      grid-template-columns: 225px 1fr;
      grid-gap: 0 1.25rem;
      gap: 0 1.25rem;
      max-width: 40rem;
  }
}

@media screen and (min-width: 768px){
  .photo {
      height: 175px;
  }
}